/* PC styles */
@media screen and (min-width: 601px) {
    .SideBar
        {   
            width:300px;
            
        }
        .emoji-icon{
            width: 20px;
            margin-right: 20px;
        }
        .BackButton{
            display: none;
        }
  }


.HomChat{
    height: 91.5vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color:aliceblue;
    background: url('../Img/AppBg.png');

    
    
    
    .container{
        border:1px solid rgb(0, 0, 0);
        border-radius: 10px;
        width: 100%;
        height: 100%;
        display:flex ;
        overflow: hidden;
        .SideBar
        {   
            
            overflow-y: scroll;
            overflow-x: hidden;
            border-right:3px solid rgb(7, 15, 87);
            background-color: rgb(2, 26, 48);
            scrollbar-width: thin;
            scrollbar-color: #6a737d #f0f0f0; /* thumb color, track color */

            &::-webkit-scrollbar {
                width: 5px;
            }

            &::-webkit-scrollbar-thumb {
                background-color: #6a737d;
                border-radius: 6px;
            }

            &::-webkit-scrollbar-track {
                background-color: #053d60d9;
            }
            

            .Search{
                border-bottom: 1px solid gray;
                .searchForm{
                   
                    .inptt{background-color: transparent;
                        border:none;
                        color:lightgray;
                        outline: none;
                        width:100%;
                        height: 50px;



                        &::placeholder{
                            color:lightgray;
                            font-size: medium;
                        }
                }
                }
                
            }
            
            .userChat{
                    padding:10px;
                    display:flex;
                    align-items: center;
                    gap:10px;
                    color:white;
                    cursor:pointer;

                    &:hover{
                        background-color: rgba(31, 27, 80, 0.76);
                    }




                .usrImg{
                    min-width: 50px;
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                
                
                }
                .userChatInfo{
                    .UsersName{font-size: 18px;font-weight: 500;}
                    .Messsagee{font-size: 14px;color: lightgray;}

                }
                


            }

        }


        .NoUserSelected{
            flex:2;
            background-color: rgba(2, 26, 48, 0.878);
            padding: 10px;
            height: 100%;
            text-align: center;
            .AppLogoNoUser{
                height:50px;
                margin-top: 240px;
            }
        }

        .Chat{
            flex:2;
            width: 100%;
            .chatInfo{
                height: 30px;
                background-color: rgb(2, 26, 48) ;
                display: flex;
                align-items: center;
                padding: 10px;
                color: lightgray;
                border-bottom: 3px solid  rgb(7, 15, 87) ;
            }
            

            

            .messages{
                background-color: rgba(2, 26, 48, 0.878);
                padding: 10px;
                height: calc(100% - 122px);
                overflow-y: scroll;
                overflow-x: hidden;
                &::-webkit-scrollbar {
                    width: 5px;
                }
    
                &::-webkit-scrollbar-thumb {
                    background-color: #6a737d;
                    border-radius: 6px;
                }
    
                &::-webkit-scrollbar-track {
                    background-color: #053d60d9;
                }
                
                .Messa{
                    display:flex;
                    gap:20px;
                    margin-bottom: 20px;

                    .messageInfo{
                        display:flex;
                        flex-direction:column;
                        color:gray;
                        font-weight:300;
                        
                        
                        
                        .senderPic{
                        width:40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    
                    }
                    .messageContent{
                        max-width: 30%;
                        display:flex;
                        flex-direction: column;
                        gap:10px;


                        .actMessage{
                            background-color: aliceblue;
                            color: black;
                            padding: 10px 20px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content;
                        }

                    .recieverPic{
                        width:50%;
                        

                    }
                    



                    }
                    &.owner{
                        flex-direction: row-reverse;

                        .messageContent{
                            align-items: flex-end;
                            

                        .actMessage{
                            background-color: rgba(27, 25, 145, 0.74);
                            color:aliceblue;
                            border-radius: 10px 0px 10px 10px;
                        }    
                    }
                }
            }



            }


            .Input{
                color:black;
                height: 30px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items:center;
                justify-content: space-between;

                .TypMsg{
                    width:100%;
                    border:none;
                    outline:none;
                    font-size:18px;

                    &::placeholder{
                        color:lightgray;
                    }
                }
                .send{
                    display:flex;
                    align-items:center;
                    gap:10px;

                    .SndBut{
                        border:none;
                        padding:10px 15px;
                        color:white;
                        background-color:rgba(12, 12, 12, 0.788);
                    }
                }
            }
            


        }

    }



}
.SndFile{
    height: 25px;
}

.userpht{
    margin-left: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    display: flex;
}

.Naam{
    font-weight: bold;
}


@media screen and (max-width: 600px) {
        
    .SideBar
        {
            width:100%;
            border-right:3px solid rgb(7, 15, 87);
            background-color: rgb(2, 26, 48);
        }
        .hidden {
            display: none;
          }
    
    
    
    .userChat{
        overflow: scroll;
    }
    .NoUserSelected{
        display:none;
    }

    .emoji-icon{
        display:none;
    }
    .Dates{
        display:none;
    }
    .BackButton{
        margin-left: 20px;
        height: 30px;
        background: linear-gradient(to right, #3498db, #2980b9);
    color: #fff;
    border: none;
    padding: 10px 15px;
    font-size:12px;
    cursor: pointer;
    border-radius: 5px;
    float: right; 
    }
    .BackButton:hover{
        background: linear-gradient(to right, #2980b9, #3498db);
    }
  }