.Nav{
    width:100%;
    background-color: black;
    color:rgb(219, 235, 240);
    height:60px;
    display: flex;    
    justify-content: space-between;
}

.AppNm{
    height:50px;
    display: flex;
    width: 100%;
}


.Imag{
    margin-top: 8px;
    margin-left: 10px;
    height:50px;
}

.p2{
    color:aliceblue;
    font-size: larger;
    margin-left: calc(85% - 280px);
}




/* .imgNav{
    position: absolute;
    background-color: black;
    height: 40px;
    border-radius: 3px;
    cursor: pointer;
} */
.imgbutton{
  margin-left: calc(90% - 280px);
}

.Lnk{
    
    font-size:small;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


.LogInbutton{
    margin-top: 10px;
    border-radius:5px;
    width: 120px;
    height: 30px;
    border: none;
    outline:none;
    background-image: linear-gradient(to right, rgb(47, 128, 175) , rgb(19, 25, 182));
    color:white;
    font-size: smaller;
    font-family:'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    cursor:pointer;
}
.LogInbutton.disabled {
    opacity: 0.5; 
    
  }

.userphto{
    margin-left: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    display: flex;
    float: right;
}

.sign-out{
    margin-top: 10px;
    border-radius:5px;
    border: none;
    outline:none;
    background-image: linear-gradient(to right, rgb(47, 128, 175) , rgb(19, 25, 182));
    color:white;
    font-size: smaller;
    font-family:'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    cursor:pointer;
}


@media screen and (max-width : 600px) {
    .imgbutton{
        margin-left: calc(90% - 280px);
    }
    .p2 {
        font-size: 12px; 
      }
      .userphto{
        margin-top: 10px;
        margin-right: 14px;
        width: 40px;
        height: 40px;
    }

    .sign-out{
        font-size: 8px;
        width: 40px;
        height: 40px;
        margin-right: 3px;
    }
}



