/* FormBanner.css */

.form-banner {
    padding: 20px;
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: rgba(8, 8, 8, 0.9);
    backdrop-filter: blur(2px);
    border: 1px solid rgb(3, 19, 51);
    border-radius: 8px;
  }
  
  .gradient-text {
    font-size: 24px;
    font-weight: bold;
    background: linear-gradient(45deg, #4caf50, #2196f3); /* Gradient from green to blue */
    -webkit-background-clip: text; /* Apply the gradient to the text */
    color: transparent; /* Make the original text transparent */
  }
  
  

  .app-banner {
    margin-bottom: 20px;
  }
  
  .app-banner img {
    max-width: 100%;
    height: auto;
  }
  
  
  
