body {
    font-family: 'Arial', sans-serif; /* Use a common sans-serif font */
    color: #333; /* Set a default text color */
  }
  .Fheading {
    text-align: center;
  color: transparent;
  background: linear-gradient(45deg, #ff0000, #0000ff); /* Gradient from red to blue */
  -webkit-background-clip: text;
  padding: 10px;
  border-radius: 8px;
  }


.signup-form-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid rgb(3, 19, 51);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: rgba(8, 8, 8, 0.9); /* Semi-transparent white background */
    backdrop-filter: blur(2px); /* Add a blur effect to the background */
  }
  

  

  .faram{
    display: flex;
    flex-direction: column;
  }
  
  .l1 {
    margin-bottom: 8px;
    color: #555;
  font-weight: bold;
  }


  .rainbow-text {
    background-image: linear-gradient(to right, violet, indigo, blue, green, yellow, orange, red);
    -webkit-background-clip: text;
    color: transparent;
    display: inline-block;
  }
  
  /* Add animation for a more dynamic effect */
  @keyframes rainbow {
    to {
      background-position: 200% 0;
    }
  }
  
  .rainbow-text-animated {
    background: linear-gradient(to right, violet, indigo, blue, green, yellow, orange, red);
    -webkit-background-clip: text;
    color: transparent;
    display: inline-block;
    background-size: 200% auto;
    animation: rainbow 2s linear infinite;
  }
  

  .inp {
    padding: 12px;
    margin-bottom: 16px;
    border: none; /* Remove default border */
    border-bottom: 2px solid #fbfbfb; /* Add a bottom border */
    /* border-radius: 0; */
    /* transition: border-color 0.3s, box-shadow 0.3s, background-color 0.3s; */
    background-color: rgba(8, 8, 8, 0.9); /* Semi-transparent white background */
    backdrop-filter: blur(1px); /* Add a blur effect to the background */
    font-size: 16px;
    color: #ffffff;
  }
  
  .inp:focus {
    border-color: #2196f3;
    box-shadow: 0 2px 4px rgba(33, 150, 243, 0.6);
    background-color: #080808c2; /* Semi-transparent white background */
    backdrop-filter: blur(1px); /* Add a blur effect to the background */
  }
  
  /* Style placeholder text */
  .inp::placeholder {
    color: #aaa;
    font-style: italic; /* Make the placeholder text italic */
  }
  
  /* Add a slight box shadow on hover for a lifted effect */
  .inp:hover {
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
  }
  
  
  
  .preview-image {
    display: block;
    max-width: 100%;
    margin-top: 10px;
    border-radius: 5px;
  }

  
  
  /* Responsive Styling */
  @media (max-width: 600px) {
    .signup-form-container {
      width: 90%;
      margin: 50px auto;
    }
  
    input {
      width: 100%;
    }
    .Login{
      overflow: scroll;
    }
  }
  